.wrapper {
    /* width: 100%; */
    display: flex;
    align-items: center;
    margin: 30px 20px;
}

.step {
    font-size: 32px;
    flex: 1;
}

.imgBackground {

    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 30px;
    flex: 1;
}

.image {
    width: 150px;
    height: 150px;
    border-radius: 30px;
    background-color: #c9edc9;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    font-size: 36px;
    font-weight: bold;
    flex: 2;
}


@media screen and (max-width:500px) {
    .step {
        font-size: 20px;
    }

    .content {
        font-size: 24px;
    }

    .image {
        width: 80px;
        height: 80px;
        border-radius: 16px;
    }
}

@media screen and (min-width:1200px) {
    .wrapper {
        width: 60%;
        margin: 20px 20%;
    }
}