@font-face {
    font-family: 'Paint';
    src: url("../Greatest Richmond Alternate One.woff2") format('woff2');
}

:root {
    --height: calc(100vh - 60px);
}

.wrapper {
    /* position: absolute; */
    box-sizing: border-box;
    /* width: 100%; */
    min-height: var(--height);
    left: 30%;
    display: flex;
    flex-direction: column;
}

.background {
    position: relative;
    display: inline-block;
    width: 100%;
    height: calc(var(--height) / 2.5);
    z-index: -10;
    top: 50%;
    background-color: #c9edc9;
}

.contentWrapper {
    position: relative;
    display: inline-block;
    top: -30%;
}

.title {
    font-size: 120px;
    line-height: 110px;
    margin-left: 30px;
}

.day {
    font-family: "Paint";
    font-size: 120px;
    line-height: 90px;
    margin-top: 20px;
    margin-left: 40px;
}

.section {
    width: 100%;
    min-height: var(--height);
}

.header {
    width: 100%;
    min-height: 30%;
    background-color: #c9edea;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.heading {
    box-sizing: border-box;
    font-family: "Paint";
    font-size: 100px;
    line-height: 110px;
    /* width: 100%; */
    padding: 20px 40px;
    margin-bottom: 20px;
}

.subheading {
    font-size: 30px;
    padding: 10px 30px;
}

.list {
    list-style: none;
    display: flex;
    height: 70%;
    flex-direction: column;
    justify-content: space-evenly;
}

.list li {
    font-size: 50px;
    margin: 40px 0px 40px 10%;
}

.paragraph {
    font-weight: bolder;
    font-size: 24px;
    line-height: 34px;
    padding: 15px 20px;
}

.reviewHeading {
    font-size: 60px;
    margin: 40px 20px;
}

@media screen and (max-width:400px) {
    .heading {
        font-size: 60px;
    }
}

@media screen and (min-width:1200px) {
    .paragraph {
        width: 60%;
        margin-left: 20%;
    }
}