.wrapper {
    width: 100%;
    height: calc(100vh);
    background-color: #c9edc9;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card {
    width: 500px;
    min-height: 400px;
    background-color: white;
    border-radius: 16px;
    box-shadow: 5px 5px 40px 4px rgba(0, 0, 0, 0.4);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.title {
    margin: 20px 0px;
    font-weight: bolder;
}

.buttonWrapper {
    display: flex;
    justify-content: space-between;
    width: 80%;
    padding: 30px 40px;
}